import { Component, EventEmitter, Prop, Event, Element, h, State, Listen } from '@stencil/core';
import { InputType } from '@globals/enums';
import { getValueByPath } from '@utils';

@Component({
  tag: 'hdc-autocomplete',
  styleUrl: 'hdc-autocomplete.scss',
})
export class HdcAutocomplete {
  /**
   * Element of hdc multiselect
   */
  @Element() el: HTMLElement;
  /**
   * Prop type of hdc input autocomplete
   */
  @Prop() type: InputType = InputType.DEFAULT;
  /**
   * Prop value of hdc input autocomplete
   */
  @Prop({ mutable: true }) value: string | object | null;
  /**
   * Prop disable of hdc input autocomplete
   */
  @Prop({ mutable: true }) disable: boolean;
  /**
   * Prop lock of hdc input autocomplete
   */
  @Prop({ mutable: true }) lock: boolean;
  /**
   * Prop placeholder of hdc input autocomplete
   */
  @Prop() placeholder: string;
  /**
   * Prop items of hdc input autocomplete
   */
  @Prop({ mutable: true }) items: Array<string | object> | undefined;
  /**
   * Prop view items of hdc input autocomplete
   */
  @Prop({ mutable: true }) viewItems: number = 5;
  /**
   * Prop key of hdc input autocomplete
   */
  @Prop() valueKey = 'name';
  /**
   * Prop string of hdc input autocomplete
   */
  @Prop() valueLabel = 'name';
  /**
   * Event change value of hdc input autocomplete
   */
  @Event() selectedValue: EventEmitter<string | object | null>;
  /**
   * State filtered items of hdc input autocomplete
   */
  @State() filteredItems: Array<string | object> = [];
  /**
   * State search text of hdc input autocomplete
   */
  @State() searchText = '';
  /**
   * State is toggle dropdown of ibk input autocomplete
   */
  @State() isToggleDropdown = false;



  /**
   * State is index selected
   */
  @State() indexSelected = 0;
  /**
   * Max height of hdc input autocomplete
   */
  maxHeight = 26;
  /**
   * Determines whether change value filter options on
   * @param event CustomEvent
   */
  onChangeValueFilterOptions(event: CustomEvent) {
    this.searchText = event.detail;
    this.indexSelected = 0;
    if (this.searchText.length > 2) {
      this.filteredItems =
        this.items?.filter((option) => {
          const label =
            this.valueLabel && typeof option === 'object' ? option[this.valueLabel] : option;

          return label.toLowerCase().includes(this.searchText.toLowerCase());
        }) ?? [];
      this.setToggleDropdown();
    } else {
      this.filteredItems = [];
    }
  }

  /**
   * Determines whether click select option on
   * @param option string
   */
  onClickSelectOption(option: string | object) {
    this.value = option;
    this.filteredItems = [];
    this.selectedValue.emit(this.value);
  }
  /**
   * Determines whether clicked icon close on
   */
  onClickedIconClose() {
    this.value = null;
    this.selectedValue.emit(this.value);
    this.indexSelected = 0;
  }
  /**
   * Renders hdc input autocomplete
   * @returns any
   */
  render() {
    return (
      <div class="hdc-input-autocomplete">
        <hdc-input
          type={this.type}
          placeholder={this.placeholder}
          disable={this.disable}
          lock={this.lock}
          uppercase={false}
          value={this.getValueKey()}
          onChangeFocus={() => this.setToggleDropdown()}
          onChangeValue={(event) => this.onChangeValueFilterOptions(event)}
        />
        {this.value && typeof this.value === 'object' && !this.disable &&
          <button class="hdc-input-autocomplete-close" onClick={() => this.onClickedIconClose()}>
            <svg width="20" height="20" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
              <path fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="1.5" d="M6.758 17.243L12.001 12m5.243-5.243L12 12m0 0L6.758 6.757M12.001 12l5.243 5.243" />
            </svg>
          </button>
        }
        <div class={this.getClassInputAutocompleteContainr()}>
          <ul class={this.getClassInputAutocomplete()} style={{ maxHeight: this.getMaxHeight() }}>
            {this.filteredItems.map((option, index) => (
              <li
                key={index}
                class={this.getClassItemFilter(index)}
                onClick={() => this.onClickSelectOption(option)}
              >
                {this.getStartTyping(option)}
              </li>
            ))}
          </ul>
        </div>
      </div>
    );
  }

  @Listen('click', { target: 'window' })
  public handlesBodyClick(ev: Event) {
    if (!ev.composedPath().includes(this.el)) {
      this.isToggleDropdown = false;
      this.indexSelected = 0;
    }
  }

  @Listen('keydown')
  handleKeyDown(ev: KeyboardEvent) {
    const allowedKeys = ['ArrowDown','ArrowUp','Enter'];
    if (allowedKeys.includes(ev.key)) {
      let active = this.el.querySelector('li.hdc-input-autocomplete-item-active') || this.el.querySelector('ul.hdc-input-autocomplete-options li');
      active?.classList.remove("hdc-input-autocomplete-item-active");

      if (ev.key === 'ArrowDown') {
        if (this.filteredItems.length > 0 && this.indexSelected < this.filteredItems.length - 1) {
          this.indexSelected = this.indexSelected + 1;
        }
        active = active?.nextElementSibling || active;
      } else if (ev.key === 'ArrowUp') {
        if (this.filteredItems.length > 0 && this.indexSelected > 0) {
          this.indexSelected = this.indexSelected - 1;
        }
        active = active?.previousElementSibling || active;
      } else {
        active = ev.target as HTMLElement;
      }

      active?.classList.add('hdc-input-autocomplete-item-active');
      const container = this.el.querySelector('ul.hdc-input-autocomplete-options');
      const currentOffset = this.getOffset(container as Element).top + (container?.clientHeight || 0);
      const nextElement = active?.nextElementSibling || active;
      const nextBottom = this.getOffset(nextElement as Element).top + (nextElement?.clientHeight || 0)
      var nextOffset = (container?.scrollTop || 0) + nextBottom - currentOffset;
      container?.scrollTo({top: nextOffset});
      
      if (ev.key === 'Enter') {
        this.onClickSelectOption(this.filteredItems[this.indexSelected]);
      }
    }
  }


  setToggleDropdown() {
    this.isToggleDropdown = this.filteredItems.length > 0;
  }

  /**
   * Gets start typing
   * @param value string | object
   * @returns any
   */
  getStartTyping(value: string | object) {
    const label = this.valueLabel && typeof value === 'object' ? value[this.valueLabel] : value;

    return label;
  }
  getStyleKeyboard(index: number) {
    return {
      'hdc-input-autocomplete-item': true,
      'hdc-input-autocomplete-item-active': index == this.indexSelected,
    };
  }
  /**
   * Gets max height
   * @returns { [key: string]: boolean }
   */
  getMaxHeight() {
    return this.viewItems > 0 ? `${this.maxHeight * this.viewItems}px` : '';
  }

  /**
   * Gets class input autocomplete containr
   * @returns { [key: string]: boolean }
   */
  getClassInputAutocompleteContainr() {
    return {
      'hdc-input-autocomplete-container': true,
      'hdc-input-autocomplete-container--top': false,
      'hdc-input-autocomplete-container--show': this.filteredItems.length > 0
    };
  }

  /**
   * Gets class input autocomplete
   * @returns { [key: string]: boolean }
   */
  getClassInputAutocomplete() {
    return {
      'hdc-input-autocomplete-options': true,
    };
  }

  getClassItemFilter(index: number) {
    return {
      'hdc-input-autocomplete-item': true,
      'hdc-input-autocomplete-item-active': index === 0,
    }
  }

  /**
   * Gets value key
   * @returns string
   */
  getValueKey() {
    return (
      this.valueKey && this.value && typeof this.value === 'object'
        ? getValueByPath(this.value, this.valueLabel.split('.'))
        : this.value
    ) as string;
  }

  private getOffset(element: Element) {
    if (!element.getClientRects().length)
    {
      return { top: 0, left: 0 };
    }

    let rect = element.getBoundingClientRect();
    let win = element.ownerDocument.defaultView;
    return (
    {
      top: rect.top + (win?.scrollY || 0),
      left: rect.left + (win?.scrollX || 0),
    });  
  }
}